.wrapper {
  padding: 30px 10px 385px 10px;
  /* margin: 0 20px 30px 20px; */
  margin: 0 auto 30px auto;
  width: 320px;
  background: url('../../../img/background/functionsMob.jpg');
  background-image: -webkit-image-set(url('../../../img/background/functionsMob.webp') 1x, url('../../../img/background/functionsMob.jp2') 1x, url('../../../img/background/functionsMob.jpg') 1x);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow: hidden;
}
.header {
  font-family: 'Caviar Dreams';
  font-size: 22px;
  line-height: 36px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  font-weight: 300;
  margin: 0 0 15px 0;
}
.text {
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
  width: 300px;
  margin: 0 auto 30px auto;
  list-style: none;
  padding-left: 16px;
}
.textItem {
  position: relative;
}
.textItem::before {
  display: block;
  position: absolute;
  top: 50%;
  left: -16px;
  transform: translate(0, -50%);
  content: '';
  width: 8px;
  height: 8px;
  background: #69C9CA;
  border-radius: 50%;
}
.textItem:last-child::before {
  top: 10px;
  transform: translate(0, 0);
}
@media(min-width: 1200px) {
  .wrapper {
    /* margin: 0 70px 70px 70px; */
    margin: 0 auto 70px auto;
    width: 1170px;
    padding: 30px 50px 385px 50px;
    background: url('../../../img/background/functions.jpg');
    background-image: -webkit-image-set(url('../../../img/background/functions.webp') 1x, url('../../../img/background/functions.jp2') 1x, url('../../../img/background/functions.jpg') 1x);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  .header {
    font-size: 48px;
    line-height: 60px;
    margin-bottom: 50px;
  }
  .text {
    font-size: 18px;
    line-height: 30px;
    width: 40%;
    margin: 0 auto;
    padding-left: 37px;
  }
  .textItem:not(:last-child) {
    margin-bottom: 10px;
  }
  .textItem::before {
    width: 13px;
    height: 13px;
    left: -24px;
  }
}
